class Notes {
  constructor() {
    const deleteNote = document.querySelectorAll(".delete-note");
    // console.log(deleteNote);
    this.events();
  }
  
  events() {
    deleteBtn =  Array.from(this.deleteNote);
    deleteBtn.map(btn => {
      btn.addEventListener("click", deleteAction);
    })
  }

  // Methods
  deleteAction(e) {
    
      console.log(e);

  
  }
 }

 export default Notes;